@import url(https://fonts.googleapis.com/css?family=K2D);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  background-color: #282c34;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-header {
  min-height: 100vh;
  /* display: flex; */
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #373739;
}

.App-link {
  color: #61dafb;
}


.color {
	background: rgba(41, 38, 23, 0.363);
	box-shadow: 5px -1px 5px 0px rgba(247, 203, 7, 0.335);
}

.text-nav {
	font-family: 'K2D', sans-serif;
	font-size: 1em;
	font-weight: bold;
	color: rgb(250, 248, 248);
}

.mir {
	font-size: 1em;
	text-shadow:
		-2px 2px 0 #000,
		1px 4px 0 #000;
}
.text-body {
	font-family: 'Montserrat', sans-serif;
}

.text-body-size {
	font-size: .8em;
}
.aboutme {
	color: #FFFFFF;
	margin-top: 6em;
	width: 100%;
    height: 100vh;
}

.photo-mir {
	/* width: 50px; */
	height: 50vh;
	margin-top: 1.5em;
	border-radius: 3em;
}



@media screen and (max-width: 588px) {
	/* .photo-mir {
		width: 25%;
		height: 10vh;

	} */
	.text-body-size {
		font-size: .7em;
	}
}
.background-home {
    background: url(/static/media/backg.153be9ab.jpg) no-repeat center center;
    background-size: cover;    
}
.background-div {
    width: 85%;
    height: 53vh;
    margin-top: 2.5em;
    border-radius: 2em
}


.home {
    width: 100%;
    height: 100vh;
}

.photo-home {
    width: 100%;
    height: 50vh;
    margin-top: .45em;
    border-radius: 2em;
}

.text-home {
    margin-top: 5em;
	font-family: 'K2D', sans-serif;
	font-size: .8m;
	font-weight: bold;
    color: rgb(250, 248, 248);
	text-shadow:
		-1px 1px 0 #000,
		1px 4px 0 #000;
}

@media screen and (max-width: 588px) {
    .photo-home {
        margin-top: 1.8em;
        /* width: 70%; */
        height: 25vh;
    }
    .text-home {
        margin-top: .5em;
    }
    .background-div {
        margin-top: 3.3em;
    }
}

